import React, { useState, Suspense, lazy, useEffect } from "react";
import * as FAIcons from "react-icons/fa";
import * as BIIcons from "react-icons/bi";

const FACustomIcon = lazy(() => import("./FACustomIcon"));
const BICustomIcon = lazy(() => import("./BICustomIcon"));

const IconsBox = (props) => {
	const [iconType, setIconType] = useState("");

	useEffect(() => {
		if (props.searchText.toLowerCase().includes("fa/")) {
			setIconType("fa");
		} else if (props.searchText.toLowerCase().includes("bi/")) {
			setIconType("bi");
		} else {
			setIconType("");
		}
	});

	let element;
	if (iconType.includes("fa")) {
		let iconsArray = Object.entries(FAIcons);
		let filteredIcons = iconsArray.filter((icon) => {
			return icon[0].toLowerCase().includes(props.searchText.toLowerCase().replace("fa/", ""));
		});

		if (props.searchText.toLowerCase() !== "" && filteredIcons.length > 0 && props.searchText.toLowerCase().replace("fa/", "").length > 0) {
			element = filteredIcons.map((icon, index) => {
				return (
					<div key={index} className="icon-box">
						{iconType.includes("fa") && <FACustomIcon icon={icon[0]} />}
					</div>
				);
			});
		} else {
			element = <div>no icons</div>;
		}
	} else if (iconType.includes("bi")) {
		let iconsArray = Object.entries(BIIcons);
		let filteredIcons = iconsArray.filter((icon) => {
			return icon[0].toLowerCase().includes(props.searchText.toLowerCase().replace("bi/", ""));
		});

		if (props.searchText.toLowerCase() !== "" && filteredIcons.length > 0 && props.searchText.toLowerCase().replace("bi/", "").length > 0) {
			element = filteredIcons.map((icon, index) => {
				return (
					<div key={index} className="icon-box">
						{iconType.includes("bi") && <BICustomIcon icon={icon[0]} />}
					</div>
				);
			});
		} else {
			element = <div>no icons</div>;
		}
	} else {
	}

	return (
		<div className="icons-container">
			<Suspense fallback={<div>Loading...</div>}>{element}</Suspense>
		</div>
	);
};

export default IconsBox;
